body, p, td, th, ul, a { background-color: #fff; background-image: none; color: #000; }
table { width: 100%; }
#navcontainer, #masthead, #globalNav, #navbar, .relatedLinks, #siteinfo, #searchbox, .footer, #footer2, .printbutton { display: none; }
#content { padding: 0; margin: 3% 5% 0 0; width: auto; float: left; border: none; }
#printheader { display: block; }
#callno { display: block; font-family: Arial, sans-serif; font-size: 80%; margin: 0 0 0 5px; }
#navBar li { border-bottom: none;}
